.Chip{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  font-size: 16px;
  color: white;
  background-color: #464646;
  width: 140px;
  height: 40px;
  border-radius: 20px;
  margin: 0.5vw;
}

.rectangle {
  text-align: center;
}

.circle {
  background-color: white;
  max-width: 40px;
  max-height: 40px;
  border-radius: 50%;
  margin-right: 8px;
}


